html {
  scroll-behavior: smooth;
  width: 100%;
  overflow: hidden auto;
}

body {
  box-sizing: border-box;
  background-color: #f2f6fa;
  max-width: 100vw;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body:has(.menu:target) {
  overflow: hidden;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #2060f6;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 50px;
  margin-bottom: 48px;
  padding: 0;
  font-size: 15px;
  font-weight: 600;
  transition: hover .3s ease-in-out;
  display: flex;
}

.btn:hover {
  color: #2060f6;
  background-color: #f2f6fa;
  border: 1px solid #2060f6;
}

.header {
  background-color: #2c2c2c;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px 0;
  display: flex;
}

@media (width >= 640px) {
  .header {
    padding: 20px 40px;
  }
}

@media (width >= 1024px) {
  .header {
    padding: 48px 54px;
  }
}

@media (width >= 1280px) {
  .header {
    padding: 48px 72px;
  }
}

@media (width >= 1600px) {
  .header {
    padding: 48px 227px;
  }
}

@media (width >= 2560px) {
  .header {
    padding: 48px 707px;
  }
}

.header__icons {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
  display: flex;
}

.header .nav {
  display: none;
}

@media (width >= 1024px) {
  .header .nav {
    display: block;
  }
}

.header .nav__list {
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0;
  list-style: none;
  display: flex;
}

.header .nav__list .nav__item {
  padding: 0 24px;
}

.header .nav .nav__link {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 13px;
  text-decoration: none;
  transition: hover 3s;
}

.header .nav .nav__link:hover {
  color: #2060f6;
}

.nav__hire-us {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  border-bottom: 2px solid #2060f6;
  width: -moz-fit-content;
  width: fit-content;
  padding-bottom: 5px;
  text-decoration: none;
  transition: hover .3s;
  display: block;
}

.nav__hire-us:hover {
  color: #2060f6;
}

.menu {
  opacity: 0;
  pointer-events: none;
  scroll-behavior: smooth;
  background-color: #2c2c2c;
  height: 100vh;
  padding: 20px;
  transition: all .3s;
  position: fixed;
  inset: 0;
}

.menu:target {
  z-index: 2;
  opacity: 1;
  pointer-events: all;
}

