.footer {
  background-color: #2C2C2C;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  z-index: 1;

  @include on-tablet {
    padding-top: 180px;
  }

  @include on-small-desktop {
    padding-top: 180px;
    margin: -15px 0;
  }

  @include on-desktop {
    padding: 180px 72px 0;
  }

  @include on-the-design {
    padding: 180px 227px 0;
  }

  @include on-large-screens {
    padding: 180px 707px 104px;
  }

  &__container {
    display: flex;
    flex-direction: column;

    @include on-small-desktop {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__top {
    padding: 72px 20px 40px;

    @include on-tablet {
      margin: 0 40px;
      padding: 0 0 120px;
    }

    @include on-small-desktop {
      margin: 0 49px;
    }

    @include on-desktop {
      margin: 0;
      padding: 0;
    }

    &-title {
      color: #fff;
      font-size: 32px;
      margin: 0 0 48px;
    }

    &-form {
      @include on-tablet {
        display: flex;
        flex-direction: column;
        align-items: left;
      }

      input {
        width: 280px;
        height: 41px;
        margin: 20px 0;
        padding: 0;
        border: none;
        border-bottom: 1px solid #fff;
        background-color: #2C2C2C;
        color: #fff;

        &::placeholder {
          color: #fff;
          opacity: 70%;
        }

        &:focus {
          border-bottom: 1px solid #2060F6;
          outline: none;
        }

        @include on-small-desktop {
          width: 370px;
        }
      }

      textarea {
        width: 280px;
        height: 41px;
        margin: 20px 0;
        padding: 0;
        border: none;
        border-bottom: 1px solid #fff;
        background-color: #2C2C2C;
        color: #fff;

        &::placeholder {
          color: #fff;
          opacity: 70%;
        }

        &:focus {
          border-bottom: 1px solid #2060F6;
          outline: none;
        }

        @include on-small-desktop {
          width: 370px;
        }
      }

      & > .btn {
        height: 56px;
        margin: 56px 0 80px;
      }
    }
  }

  &__bottom {
    margin: 40px 20px 80px;

    @include on-tablet {
      margin: 0 40px;
      padding: 0 0 139px;
    }

    &-title {
      font-size: 32px;
      font-weight: 600;
      color: #fff;
      margin: 0 0 48px;
    }

    &-contacts {
      display: flex;
      flex-direction: column;
      gap: 40px;

      &-wrapper {
        display: flex;
        flex-direction: column;

        &-socials {
          display: flex;
          flex-direction: row;
          gap: 32px;

          &-facebook:hover {
            filter: invert(60%) sepia(76%) saturate(1629%) hue-rotate(206deg) brightness(79%) contrast(115%);
          }

          &-twitter:hover {
            filter: invert(60%) sepia(76%) saturate(1629%) hue-rotate(206deg) brightness(79%) contrast(115%);
          }

          &-instagram:hover {
            filter: invert(60%) sepia(76%) saturate(1629%) hue-rotate(206deg) brightness(79%) contrast(115%);
          }
        }

        &--title {
          color: #fff;
          opacity: 50%;
          text-transform: uppercase;
          letter-spacing: 2px;
          font-size: 13px;
          padding-bottom: 8px;
        }

        &--content {
          text-decoration: none;
          color: #fff;
          font-size: 25px;
          font-family: Open-Sans, sans-serif;
          font-weight: 700;
          padding-right: 20px;

          &:hover {
            color: #2060F6;
          }
        }
      }
    }
  }
}

.footer__nav {
  padding: 0 20px 80px;

  @include on-tablet {
    display: flex;
    flex-direction: column;
    padding: 0 40px 80px;
  }

  @include on-small-desktop {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 49px;
    padding: 0 0 84px;
  }

  @include on-desktop {
    margin: 0;
  }

  .nav {
    &__list {
      list-style: none;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 0;

      @include on-tablet {
        display: flex;
        flex-direction: row;
        margin: 0;
      }

      @include on-small-desktop {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
      }

      .nav__item {
        padding: 12px 0;

        @include on-tablet {
          padding: 24px 0;
        }

        @include on-small-desktop {
          padding: 0 24px;
        }
      }
    }

    .nav__link {
      font-size: 13px;
      color: #fff;
      text-transform: uppercase;
      letter-spacing: 2px;
      text-decoration: none;
      transition: hover 0.3s;

      &:hover {
        color: #2060F6;
      }
    }
  }
}
